import Service from '@/service/art-cycle';
import * as artCycleService from '@/service/art-cycle'
import { emitter } from '@/event-bus'

const artCycle = {
    namespaced: true,
    state: () => ({
        cyclesSummary: [],
        patient: null,
        partner: null,
        cycleTypes: null,
        selectedCycle: null,
        selectedCycleNo: null,
        cycleTypeOptions: null,
        spermSourceOptions: null,
        eggSourceOptions: null
    }),
    mutations: {
        SET_CYCLE_SUMMARY(state, payload) {
            state.cyclesSummary = payload;
        },
        SET_PATIENT_DETAIL(state, payload) {
            state.patient = payload;
        },
        SET_PARTNER_DETAIL(state, payload) {
            state.partner = payload;
        },
        SET_CYCLE_TYPES(state, payload) {
            state.cycleTypes = payload;
        },
        SET_SELECTED_CYCLE(state, payload) {
            state.selectedCycle = payload
        },
        SET_SELECTED_CYCLE_NO(state, payload) {
            state.selectedCycleNo = payload;
        }
    },
    getters: {
        getSelectedCycle: ({ cyclesSummary, selectedCycleNo }) => {
            let index = cyclesSummary.findIndex(item => item.cycleNo === selectedCycleNo);
            if (index != -1) {
                return cyclesSummary(index)
            } else {
                return null;
            }
        }
    },
    actions: {
        async getCyclesDetails({ commit }, demographicNo) {
            const {
                petientDetails,
                partnerDetails,
                cyclesSummery,
                cycleTypes,
            } = await artCycleService.getCycleSummaryDetail(demographicNo);

            commit('SET_CYCLE_SUMMARY', cyclesSummery);
            commit('SET_PATIENT_DETAIL', petientDetails);
            commit('SET_PARTNER_DETAIL', partnerDetails);
            commit('SET_CYCLE_TYPES', cycleTypes);

            emitter.emit("artCycleStore:detail-loaded", cyclesSummery);
        },
        updateSelectedCycle({ state }, cycle) {
            state.selectedCycle = cycle;
        },
        updateCycle({ state }, cycle) {
            let index = state.cyclesSummary.findIndex(item => item.cycleId === cycle.cycleId);
            Object.assign(state.cyclesSummary[index], {
                ...cycle,
                cycleCompleted: cycle.complete
            })
        },
        nextCycle({ state, commit }) {
            let index = state.cyclesSummary.findIndex(item => item.cycleNo === (state.selectedCycle.cycleNo - 1));
            if (index != -1) {
                commit("SET_SELECTED_CYCLE", state.cyclesSummary[index])
            }
        },
        previsouCycle({ state, commit }) {
            let index = state.cyclesSummary.findIndex(item => item.cycleNo === (state.selectedCycle.cycleNo + 1));
            if (index != -1) {
                commit("SET_SELECTED_CYCLE", state.cyclesSummary[index])
            }
        },
        getCycleFormFieldOptions({ state }) {
            if (!state.cycleTypeOptions || !state.spermSourceOptions || !state.eggSourceOptions) {
                Service.getFormFieldOptions().then((resp) => {
                    state.cycleTypeOptions = resp.data.cycleTypes;
                    state.spermSourceOptions = resp.data.spermSources;
                    state.eggSourceOptions = resp.data.eggSource;
                });
            }
        }
    }
}

export {
    artCycle
}